import React, { Component} from 'react';
import ReactDOMServer from 'react-dom/server';
import BoxLayout from "../../components/common/BoxLayout";
import Box from "../../components/common/Box";

class SSR_Publishers extends Component {

    constructor(props) {
        super(props);
    }

    render() {
        return <React.Fragment>
            <div className="container">
                <BoxLayout>
                    {
                        this.props.data.map((data) => {
                            let url = '';
                            return (
                                <div className="col-md-4 row section-box mb-4">
                                    <Box
                                        url={url}
                                        box_class="saved-papers-box"
                                        icon_class="fa-file-text"
                                        box_title={data.name}
                                        parent_class="col-lg-12"
                                        data={data}
                                    />
                                </div>
                            )
                        })
                    }
                </BoxLayout>

            </div>
        </React.Fragment>
    }
}

let { publishers } = context;

const html = ReactDOMServer.renderToString(
    <SSR_Publishers loading={false} data={publishers}  />
);

dispatch(html);
